footer {
    margin-top: 80px;
}

header{
    display: flex;
    flex-wrap: wrap;
    background-color: yellow;
    color: blue;
    margin: 0;
    padding: 0;
    height: 100px;
}

.header{
    text-align: center;
    margin-left: 30%;
    font-size: 55px;
}
ul{
    text-align: left;
    background-color: blue;
    color: white;
    width: 100px;
    text-decoration: none;
    list-style: none;
}

.navBar{
    color: white;
    text-decoration: none;
}
.homeImg {
    width: 200px;
    height: 300px;
    padding: 5px;
}

.hcard {
    background-color: red;
    color: yellow;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    width: 400px;
    text-align: center;
}

.hcard:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
    padding: 2px 16px;
}

.userpage{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 5s linear;
            animation: App-logo-spin infinite 5s linear;
    height: 10vmin;
    pointer-events: none;
  }

@-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
}
.main{
    display: flex;
    flex-wrap: wrap;
}

.card{
    background-color: red;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 5px;
    margin-bottom: 6px;
    font-size: 18px;
    color: yellow;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    height: 400px;
    width: 300px;
    text-align: center;
    border-radius: 5px;
}

.cardDs{
    background-color: blue;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 18px;
    color: yellow;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    height: 400px;
    width: 300px;
    text-align: center;
    border-radius: 5px;
}

.cardImage{
    width: auto;
    height: 175px;
    padding: 5px;
}

.card:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.cardDs:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

h4{
    text-transform: uppercase;
    font-weight: bold;
    font-size: 24px;
    text-decoration: underline;
}

button{
    background-color: blue;
    font-size: 16px;
    padding: 10px 24px;
    color: yellow;
}

button:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.detailedButton{
    padding: 5px;
}
.addShoeForm {
    background-color: yellow;
    width: 50%;
    height: 400px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
    text-align: center;
}

.select-css {
    font-size: 16px;
    text-align: center;
    font-weight: 700;
    color: blue;
    line-height: 1.3;
    padding: .6em 1.4em .5em .8em;
    width: 150px;
    max-width: 100%; 
    height: 60px;
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: .5em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: red;
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0;
    background-size: .65em auto, 100%;
}
.select-css::-ms-expand {
    display: none;
}
.select-css:hover {
    border-color: #888;
    background-color: yellow;
}
.select-css:focus {
    border-color: #aaa;
    box-shadow: 0 0 1px 3px blue;
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222; 
    outline: none;
}
.select-css option {
    font-weight:normal;
}

.shoeName{
    text-transform: uppercase;
    text-align: center;
    -webkit-text-decoration: yellow underline;
            text-decoration: yellow underline;
    background-color: red;
    font-size: 50px;
    width: 50%;
    margin-left: 25%
}

.first {
    float: left;
    width: 55%;
    background-color: blue;
    margin-left: 5%;
    margin-right: 5%;
    border-radius: 25px;
    color: yellow;
}

.second{
    float: right;
    background-color: yellow;
    border-radius: 25px;
    font-size: 20px;
    width: 25%;
    margin-right: 5%;
    color: blue;
    padding: 10px;
}
  
.row:after {
    content: "";
    display: table;
    clear: both;
}

.detailImg {
    margin-top: 10px;
